// import logo from "./logo.svg";
import "bootstrap/dist/css/bootstrap.min.css";
import "jquery/dist/jquery.min.js";
import "bootstrap/dist/js/bootstrap.min.js";
import { useState } from "react";
import "./App.css";
import About from "./components/About";
import Navbar from "./components/Navbar";
import Textform from "./components/Textform";
import Alertmessage from "./components/Alertmessage";
import Land from "./components/Land";

function App() {
  const [mode, setmode] = useState("light");
  const [style, setstyle] = useState({
    color: "black",
    backgroundColor: "white",
  });
  // setInterval(() => {
  //   document.title = "Text Utils is Amazing";
  // }, 500);
  // setInterval(() => {
  //   document.title = "Install Text-Utils";
  // }, 1200);
  const changemode = () => {
    if (mode === "light") {
      setmode("dark");
      document.body.style.backgroundColor = "rgb(40,40,40)";
      document.body.style.color = "white";
      setstyle({
        color: "white",
        backgroundColor: "black",
      });
      setstate({
        visible: true,
      });
      document.title = `Text-Utils Dark mode`;
      setTimeout(() => {
        setstate({
          visible: false,
        });
      }, 1500);
    } else {
      setmode("light");
      document.body.style.backgroundColor = "white";
      document.body.style.color = "black";
      setstyle({
        color: "black",
        backgroundColor: "white",
      });
      setstate({
        visible: false,
      });
      document.title = `Text-Utils`;
    }
  };

  const [state, setstate] = useState({
    visible: false,
  });

  const togglealert = () => {
    setstate({
      visible: false,
    });
  };
  return (
    <>
      {/* <Router> */}
      <Navbar title={"TextUtils"} changemode={changemode} mode={mode} />
      <Alertmessage togglealert={togglealert} state={state} />
      {/* <Switch> */}
      <Land />
      {/* <Route exact path="/about"> */}
      <Textform />
      <About exact style={style} />
      {/* </Route> */}
      {/* <Route exact path="/textform"> */}
      {/* </Route> */}

      {/* <Route exact path="/"> */}
      {/* </Route> */}
      {/* </Switch> */}
      {/* // </Router> */}
    </>
  );
}

export default App;
