import React from "react";
import { useState } from "react";

export default function Textform(props) {
  const [text, setText] = useState("");
  const clicked = (e) => {
    setText(text.toUpperCase());
    e.preventDefault();
  };
  const handleOnchange = (e) => {
    setText(e.target.value);
    e.preventDefault();
  };
  const clickedlow = (e) => {
    setText(text.toLocaleLowerCase());
    e.preventDefault();
  };
  const cleartext = (e) => {
    setText("");
    e.preventDefault();
  };
  const gettext = (e) => {
    const newtext = document.querySelector("#mybox");
    newtext.className = "newtext";
    e.preventDefault();
  };
  const gettext2 = (e) => {
    const newtext2 = document.querySelector("#mybox");
    newtext2.className = "newtext2";
    e.preventDefault();
  };

  const handlespace = (e) => {
    const newtxt = text.split(/[  ]+/);
    setText(newtxt.join(" "));
    e.preventDefault();
  };

  return (
    <>
      <div className="container  ">
        <form>
          <div className="form-group">
            <label htmlFor="mybox"></label>
            <h3>{props.heading}</h3>
            <textarea
              style={props.style}
              className="form-control mb-3"
              value={text}
              onChange={handleOnchange}
              id="mybox"
              rows="10"
            ></textarea>
          </div>
          <div className="btngroup">
            <button className="btn btn-primary" onClick={clicked}>
              Convert Uppercase
            </button>
            <button className="btn btn-success mx-1" onClick={clickedlow}>
              Convert Lowercase
            </button>
            <button className="btn btn-danger mx-1" onClick={cleartext}>
              Clear Text
            </button>
            <button className="btn btn-secondary rightbtn" onClick={gettext}>
              Bold
            </button>
            <button className="btn btn-secondary leftbtn" onClick={gettext2}>
              Light
            </button>
            <button className="btn btn-info mx-2" onClick={handlespace}>
              Remove spaces
            </button>
          </div>
        </form>
      </div>
      <div className="container textformbox">
        <h2 className="my-2">Your Text Summary</h2>
        <p>
          <b>{text.split(/[ ]+/).length - 1}</b> Words and <b>{text.length}</b>{" "}
          Characters
        </p>
        <p>{0.01 * text.split([" "]).length - 0.01} Minutes to read</p>
        <p>Sallaybles {text.split(/[.?!]+/).length - 1}</p>
        <h3>Preview</h3>
        <p>{text}</p>
      </div>
    </>
  );
}
